body {
  margin: 0;
  padding: 0;
  
  font-family:  'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.centered {
        text-align: center;
        position: absolute;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        -webkit-animation-delay: .25s;
                animation-delay: .25s;
      
}

.fullstack {
  color: darkslategray;
}

.name {
  color: gray;
}
/* Extra small devices*/
@media only screen and (max-width: 600px){
  .centered {
    top: 15%;
  }
  .fullstack{
    font-size: 28px;
  }
  .name {
    font-size: 15px;
  }

}
/* Small devices*/

@media only screen and (min-width: 600px){
  .centered {
    top: 25%;
  }
  .fullstack{
    font-size: 40px;
  }
  .name {
    font-size: 20px;
    
  }
}

/* Medium devices */
@media only screen and (min-width: 768px){
  .centered {
    top: 30%;
  }
  .fullstack{
    font-size: 50px;

  }
  .name {
    font-size: 25px;
    
  }
}

/* Large devices like laptops or desktops */
@media only screen and (min-width: 992px){
  .centered {
    top: 40%;
  }
  .fullstack{
    font-size: 60px;
  }
  .name {
    font-size: 30px;
    
  }
}

/* Extra large devices like large laptops or desktops */
@media only screen and (min-width: 1200px){
  .centered {
    top: 50%;
  }
  .fullstack{
    font-size: 70px;

  }
  .name {
    font-size: 40px;
    
  }
}


.overlay{
    background-color: whitesmoke;
  
}
.responsive-image {
    position: relative;
    max-width: 100%;
    opacity: 0.6;
 
  }
  
  .responsive-image__image {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }


  
@media(min-width:468px){
    
    .thisContainer{
        display: flex;
        justify-content: center;
        


    }

}

.thisContainer{
     margin-left: 25%;
     margin-right: 25%;
     padding-top: 1%;
     padding-bottom: 1%;
}

.thisBox{
    flex-basis: 25%;
    text-align: center;
    padding: 3%;
  
    
}


.bar{
    background-color: lightgray;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;


}




.thisBox a:link{
    color: white;
